import '../../App.css';
import React from 'react';
import Grid from '@material-ui/core/Grid';
import CssBaseline from '@material-ui/core/CssBaseline';
import { makeStyles } from '@material-ui/core/styles';
import {IconButton} from "@material-ui/core";
import {Link} from 'react-router-dom';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import MenuIcon from '@material-ui/icons/Menu';

import 'date-fns';
import DateFnsUtils from '@date-io/date-fns';
import {
    MuiPickersUtilsProvider,
    KeyboardTimePicker,
    KeyboardDatePicker,
} from '@material-ui/pickers';

import TextField from '@material-ui/core/TextField';
import SectionBlockMini from "../../components/SectionBlockMini";

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        width: "100%",
        backgroundColor: 'rgb(255,255,255)',
    },

    hrUp: {
        borderBottom: "1px solid rgba(255, 255, 255, 0.3)",
        border: 0,
        width: "85%",
    },

    hrDown: {
        margin: "20px auto",
        borderTop: "1px solid rgba(0, 0, 0, 0.1)",
        borderBottom: "1px solid rgba(255, 255, 255, 0.3)",
        border: 0,
        height: 0,
        maxWidth: "45%",
        [theme.breakpoints.down('600')]: {
            maxWidth: "90%",
        },
    },

    UpperBlock: {
        background: 'rgb(22,111,255)',
        padding: 15,
        borderBottomRightRadius: 50,
    },

    header: {
        fontSize: 24,
        fontWeight: "bold",
        color: 'rgb(255,255,255)',
    },

    title: {
        fontSize: 15,
        fontWeight: "bold",
        textAlign: "left",
        paddingLeft: 20,
        paddingBottom: 10,
    },

    subtitle: {
        fontSize: 12,
        fontWeight: "bold",
        textAlign: "left",
        paddingLeft: 20,
        paddingBottom: 5,
        paddingRight: 25,
        paddingTop: 5,
        color: 'rgb(113,113,113)',
    },

    date_text: {
        fontSize: 12,
        color: 'rgb(113,113,113)',
        textTransform: 'uppercase',
        paddingTop: 25,
    },

    date_value: {
        fontSize: 18,
        color: 'rgb(113,113,113)',
        fontWeight: "bold",
    },

}));

function App() {
    const classes = useStyles();
    const [selectedDate, setSelectedDate] = React.useState(new Date('2021-07-11T21:11:54'));

    const handleDateChange = (date) => {
        setSelectedDate(date); };

    return (

        <div className="App">
            <header className="App-header">
                <React.Fragment>
                    <div className={classes.root}>
                        <Grid container direction="column" className={classes.UpperBlock}>
                            <Grid container justifyContent="space-between">
                                <Grid item>
                                    <Link to="/">
                                        <IconButton>
                                            <ArrowBackIosIcon fontSize="large" style={{ color: 'rgb(159,191,244)' }}/>
                                        </IconButton>
                                    </Link>
                                </Grid>
                                <Grid item>
                                    <IconButton aria-label="delete">
                                        <MenuIcon fontSize="large" style={{ color: 'rgb(159,191,244)' }}/>
                                    </IconButton>
                                </Grid>
                            </Grid>
                            <hr className={classes.hrUp}/>
                            <Grid item><div className={classes.header}>Объем выполненных работ</div></Grid>
                        </Grid>
                        <hr className={classes.hrDown}/>
                        <Grid container justifyContent="center" alignItems="center" direction="column" >
                            <div className={classes.title}>Плита перекрытия тех. этажа на отм. +7,900 арм. 111 кг/м3</div>
                            <div className={classes.subtitle}>Устройство перекрытий безбалочных толщиной: 200 мм</div>
                        </Grid>
                        <hr className={classes.hrDown}/>
                        <Grid container justifyContent="space-around">
                            <MuiPickersUtilsProvider utils={DateFnsUtils}>
                            <KeyboardDatePicker
                                margin="normal"
                                id="date-picker-dialog"
                                label="Выберите дату:"
                                format="dd/MM/yyyy"
                                value={selectedDate}
                                onChange={handleDateChange}
                                KeyboardButtonProps={{
                                    'aria-label': 'change date',
                                }}
                            />
                            </MuiPickersUtilsProvider>
                        </Grid>
                        <hr className={classes.hrDown}/>
                        <Grid container justifyContent="center">
                            <Grid item xs={10} sm={6} md={5} lg={3}>
                                <TextField id="outlined-basic" label="Введите объем выполненных работ" fullWidth variant="outlined" />
                            </Grid>
                        </Grid>
                        <hr className={classes.hrDown}/>
                        <Grid container justifyContent="center">
                            <SectionBlockMini title={'Осталось к выполнению'} value={29.16} />
                            <SectionBlockMini title={'Ср. производительность'} value={3.24} />
                        </Grid>
                        <Grid container justifyContent="center" direction="column">
                            <div className={classes.date_text}>Прогнозируемая дата завершения</div>
                            <div className={classes.date_value}>20 июля 2021</div>
                        </Grid>
                    </div>
                </React.Fragment>
                <CssBaseline />
            </header>
        </div>
    );
}
export default App;
